import {Component, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-header-filter',
  templateUrl: './header-filter.component.html',
  styleUrls: ['./header-filter.component.css']
})
export class HeaderFilterComponent implements OnInit {

  @Input()
  vinCount: number = 7;
  isActived: boolean = false;
  clickActive: boolean = false;
  filterItems: any[];
  constructor() {
    this.filterItems = [
      {key: '默认排序', isActived: true, hasScrow: false, clickActive: true, isDown: false},
      {key: '首付', isActived: false, hasScrow: true, clickActive: false, isDown: false},
      {key: '月供', isActived: false, hasScrow: true, clickActive: false, isDown: false}
    ];
  }

  ngOnInit() {
  }

  labelOver(item: any) {
    if (!item.clickActive) {
      item.isActived = true;
    }
    return false;


  }
  labelLeave(item: any) {
    if (!item.clickActive) {
      item.isActived = false;
    }
    return false;
  }
  labelClick(item: any) {
    this.filterItems.forEach(
      (i) => {
        if (i.key === item.key) {
          return false;
        } else {
          i.isActived = false;
          i.clickActive = false;
          i.isDown = false;
        }
      }
    )
    if (!item.clickActive) {
      item.clickActive = true;
    } else {
      item.isDown = !item.isDown;
    }
  }
}
